@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
*{
  margin:0;
  padding:0;
  border:0;
  outline:0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root{
  --color-bg:#b7303b;
  --color-bg-variant:#a52d37;
  --color-primary:#273037;
  --color-white:#fff;
  --color-light:#CDC9C2;

  --container-width-lg:75%;
  --container-width-md:86%;
  --container-width-sm:90%;

  --transition:all 400ms ease;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(#b7303b,#a52d37);
  background: linear-gradient(var(--color-bg),var(--color-bg-variant));
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
}

/* General styles */

.container{

  width:75%;

  width:var(--container-width-lg);
  margin:0 auto;

}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size:3.5rem;
  font-weight: 900;
}

section{
  margin-top: 8rem;
  height: 100vh;
}

section>h2,section>h5{
  text-align: center;
  color: #CDC9C2;
  color: var(--color-light);
}

section>h2{
  color: #273037;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color:#CDC9C2;
  color:var(--color-light);
}

a{
  color:#273037;
  color:var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover{
  color:#fff;
  color:var(--color-white);
}

.btn{
  width: -webkit-max-content;
  width: max-content;
  display:inline-block;
  color:#273037;
  color:var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #273037;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover{
  background:#fff;
  background:var(--color-white);
  color:#b7303b;
  color:var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background:#273037;
  background:var(--color-primary);
  color:#b7303b;
  color:var(--color-bg);
}

img{
  display:block;
  width: 100%;
  height: 100%;
  object-fit: none;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px){
  .container{
    width:86%;
    width:var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width:600px){
  .container{
    width:90%;
    width:var(--container-width-sm);
  }

  section>h2{
    margin-bottom: 2rem;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
    height:100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA buttons */

.cta{
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
}

/* SOCIALS */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left:0;
    bottom: 3rem;
}

/*.header__socials::after{
    content: '';
    width: 3px;
    height: 2rem;
    background: var(--color-white);
}*/

/* me */

.me{
    background:linear-gradient(var(--color-primary),transparent);
    width: 65rem;
    height: 100vh;
    position: relative;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 50% 50% 50% 50% / 17% 17% 83% 83% ;
    
    overflow: hidden;
}
.me img{
    position: fixed;
    bottom: 100px;
}




nav{
    background-color: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 2rem;
    display: flex;
    grid-gap:0.8rem;
    gap:0.8rem;
    border-radius: 2rem;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

nav a{
    background:transparent;
    padding:0.9rem;
    border-radius: 50%;
    display: flex;
    color:var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

#logo{
    max-height:2rem;
    object-fit: contain;
    -webkit-transform: translateY(35%);
            transform: translateY(35%);
}

nav a.active{
    background: var(--color-bg);
    color:var(--color-white)
}





